@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  padding-left: 10%;
  padding-right: 10%;
  padding-top: 100px;
  padding-bottom: 60px;
  max-width: 700px;
  margin: auto;
  font-family: Roboto, sans-serif;
  color: #2d2e33;
  line-height: 1.6;
}

h1, h2, h3, h4 {
  font-weight: 500;
}

input {
  padding: 20px 16px 20px 16px;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  text-overflow: ellipsis;
  background: #fafafa;
  width: 100%;
  border-radius: 3px;
  border-color: #e6e6e6;
  border-style: solid;
  border-width: 1px;
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
}

header {
  padding-bottom: 64px;
}

.game_button {
  background-color: #f8f8f8;
  padding: 20px 16px 20px 16px;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  width: 100%;
  border-style: none;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  white-space: nowrap;
  overflow: hidden;
}

.hide_info .game_button {
  background-color: #00000000;
}

.game_button:hover {
  background-color: #f8f8f8;
}

.game_title {
  display: -webkit-flex;
  display: flex;
  min-width: 0;
}

.game_title img {
  width: 16px;
  margin: 6px;
}

.game_title p {
  margin: auto;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.game_vendor {
  display: -webkit-flex;
  display: flex;
  height: 26px;
}

.game_vendor img {
  width: 20px;
  margin: 0px;
  padding: 0 6px 0 6px;
}

.spaced {
  margin-bottom: 64px;
}

.game {
  border-bottom: 1px solid #f8f8f8;
}

.game_info {
  opacity: 1;
  padding: 16px;
  max-height: 2400px;
  overflow-y: hidden;
  transition: all 250ms ease-in-out;
}

.hide_info .game_info {
  opacity: 0;
  max-height: 0px;
  padding-top: 0;
  padding-bottom: 0;
  transition: all 250ms ease-in-out;
}

.tag {
  background-color: #e7e7e7;
  padding: 3px 10px 3px 10px;
  margin: 4px;
  border-radius: 2px;
  display: inline-block;
  font-size: 15px;
  line-height: 1.6;
}

.spacing {
  height: 16px;
}

.blue {
  background-color: #83caf3;
}

.game_info img {
  padding-right: 8px;
  width: 20px;
}

.test_gpu {
  margin-top: 30px;
}

.test_gpu * {
  vertical-align: middle;
  display: inline;
  margin: 0;
}

